@charset "UTF-8";
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald Medium";
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0 auto;
  padding: 0;
  font-size: 14px;
  line-height: 1.29;
  font-family: "Arial", sans-serif;
  color: #444444;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  white-space: nowrap;
  /* added line */
  clip: rect(1px, 1px, 1px, 1px);
}

.tablet-only {
  display: none;
}

@media (min-width: 768px) and (max-width: 1299px) {
  .tablet-only {
    display: block;
  }
}
.button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  /* IE11 fix */
  min-height: 40px;
  padding: 11px;
  font-size: 16px;
  line-height: 1.25;
  font-family: "Oswald", "Arial", sans-serif;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #68b738;
  border: none;
  border-radius: 0;
  cursor: pointer;
}
.button:hover, .button:focus {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--gray {
  color: #444444;
  background-color: #f2f2f2;
}
.button--gray:hover, .button--gray:focus {
  background-color: #ebebeb;
}
.button--gray:active {
  color: rgba(68, 68, 68, 0.3);
}

/* --------- Tablet --------- */
@media (min-width: 768px) {
  .button {
    font-size: 16px;
    line-height: 1;
  }
}
