  font-size: 16px;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  border: none;
  box-shadow: 0 0 0 1px rgba(205, 205, 205, 0.5) inset;
  appearance: textfield;
}

.form__label-icon {
  position: absolute;
  top: 50%;
  right: 13px;
  transform: translateY(-50%);
  fill: #222222;
}

.form__text-input::placeholder,
.form__textarea::placeholder {
  font-family: "Oswald", "Arial", sans-serif;
  color: #444444;
  opacity: 1;
}

.form__text-input:hover,
.form__textarea:hover {
  box-shadow: 0 0 0 1px rgb(205, 205, 205) inset;
}

.form__text-input:focus,
.form__textarea:focus {
  outline: none;
  box-shadow: 0 0 0 2px #444444 inset;
}

.form__text-input:focus::placeholder,
.form__textarea:focus::placeholder {
  color: transparent;
}

.form__text-input:focus ~ .form__label-icon {
  fill: #68b738;
}

.form__text-input--error:invalid,
.form__text-input--error:invalid:focus {
  box-shadow: 0 0 0 2px #ff8282 inset;
}

.form__text-input--error:invalid ~ .form__label-icon,
.form__text-input--error:invalid:focus ~ .form__label-icon {
  fill: #ff8282;
}

.form__textarea-label {
  display: flex;
  flex-direction: column;
}

